import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store'
import i18n from '@/translations/i18n'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: () => import('@/views/home'),
    meta: { needAuth: true, title: 'home' },
  },
  {
    path: '*',
    component: () => import('@/views/404'),
    meta: { needAuth: true },
  },
  {
    path: '/sign-in',
    name: 'sign-in',
    component: () => import('@/views/sign'),
    meta: { needAuth: false, title: 'signIn', tab: 0 },
  },
  {
    path: '/registration',
    name: 'registration',
    component: () => import('@/views/sign'),
    meta: { needAuth: false, title: 'registration', tab: 1 },
  },
]

const router = new VueRouter({
  mode: 'history',
  routes,
})

router.beforeEach((to, from, next) => {
  let title = ''
  for (const route of to.matched) {
    if (route.meta.title) {
      title += i18n.t('route.' + route.meta.title) + ' / '
    }
  }
  document.title = title.substring(0, title.length - 3)

  if (
    to.matched.some((route) => route.meta.needAuth) &&
    !store.state.auth.isAuth
  ) {
    next('/registration')
  } else {
    next()
  }
})

export default router

