const state = {
  layout: '',
  noScrollClassName: 'no-scroll',
}

const getters = {
  layout(state) { return state.layout || 'default' },
}

const actions = {
  changeLayout({ commit }, name) { commit('setLayout', name) },
  showScrolls({ state }) {
    const classList = document.body.classList
    if (classList.contains(state.noScrollClassName)) {
      document.body.classList.remove(state.noScrollClassName)
    }
  },
  hideScrolls({ state }) {
    const classList = document.body.classList
    if (!classList.contains(state.noScrollClassName)) {
      document.body.classList.add(state.noScrollClassName)
    }
  },
}

export default { state, getters, actions }
