<template lang="pug">
  component(:is="layout + '-layout'")
    router-view
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  name: 'App',
  components: {
    'default-layout': () => import('@/layouts/default'),
  },

  computed: {
    ...mapGetters('ui', ['theme']),
    layout() {
      return this.$route.meta.layout || 'default'
    },
  },

  mounted() {
    this.setThemeAttr()
  },

  methods: {
    setThemeAttr() {
      document.documentElement.setAttribute('data-theme', this.theme)
    },
  },

  watch: {
    theme() {
      this.setThemeAttr()
    },
  },
}
</script>
